import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import axios from 'axios';
import isLoginStore from "../../../Store/isLoginStore";
import placeStore from "./search/placeStore"
import store from "../../../Store/useStore"

import HeaderComponent from "./Header";

const Header = () => {

  // 로그인 확인하는 변수
  const isState = isLoginStore();
  // 현재 경로 확인하기윈한 변수
  const location = useLocation();
  // 장소 검색되었는지 확인하기위한 변수
  const { searchPlace } = placeStore(state => state);
  // 유저 정보창 open/close
  const [userInfo, set_userInfo] = useState(false);
  const setUserInfo = (e) => {
    e.preventDefault();
    set_userInfo(!userInfo);
  };

  const last_community_id = 30000;
  useEffect(() => {
    set_userInfo(false);
  }, [location.pathname, searchPlace]);

  // 로그인한 유저 정보 가져오기
  const state = store();

  return (
    <>
      <HeaderComponent
        pathname={location.pathname}
        islogin={isState.isLogin}
        userInfo={userInfo}
        setUserInfo={setUserInfo}
        state={state}
        last_community_id={last_community_id}
      ></HeaderComponent>
    </>
  );
};

export default Header;
