import React from "react";
import { Link } from "react-router-dom";
import HeaderStyle from "./HeaderStyled";
import SearchBox from "./search";
import UserInfoComponent from "./userinfo";

const HeaderComponent = ({ pathname, islogin, userInfo, setUserInfo, state, last_community_id }) => {
  return (
    <HeaderStyle>
      <div className="header_left">
        <Link to="/">
          <img src="assets/icon/logo.png" alt="로딩중입니다."></img>
        </Link>
      </div>

      <div className="header_middle">
        {pathname === "/" ? <SearchBox /> : <></>}
        <Link
          to={{
            pathname: `/community`,
            state: { props: last_community_id }
          }}
          style={{
            textDecoration: "none",
            color: "#1a786a",
            marginLeft: "20px",
          }}
        >
          커뮤니티
        </Link>
      </div>

      <div className="header_right">
        <div className="header_right_user">
          {islogin ? (
            <>
              {state.profile_url === null ? (
                <img style={{borderRadius: '50%'}}
                src="./assets/icon/userImg.png"
                alt="icon"
                onClick={(e) => setUserInfo(e)} />
              ) : (
                <img style={{borderRadius: '50%'}}
                src={state.profile_url}
                alt="icon"
                onClick={(e) => setUserInfo(e)} />
              )}
              {state.user_name} {userInfo ? <UserInfoComponent /> : <></>}
            </>
          ) : (
            <>
              <Link
                to="/sign-up"
                style={{ textDecoration: "none", color: "#1a786a" }}
              >
                회원가입
              </Link>
              <div className="header_right_bar">|</div>

              <Link
                to="/login"
                style={{ textDecoration: "none", color: "#767676" }}
              >
                로그인
              </Link>
            </>
          )}
        </div>
      </div>
    </HeaderStyle>
  );
};

export default HeaderComponent;
