import { uploadBytes, ref, getDownloadURL } from "firebase/storage";
import { useLocation } from "react-router-dom";
import store from "../../../../Store/useStore"; //로그인 된 유저 정보
import AddHomeComponent from "./addHome"; //숙소추가화면
import React, { useState, useEffect } from "react";
import { storage } from "./addFB";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const AddhomePage = () => {
  const user = store(); //user token
  const navigate = useNavigate(); //페이지 이동
  const today = new Date();
  const now = today.toLocaleString;
  const location = useLocation(); //메인페이지에서 클릭한 마커 정보 가져옴
  const [ImagePre, setImagePre] = useState({
    //이미지 미리보기용 변수
    img1: "",
    img2: "",
    img3: "",
  });
  //   const [list, setList] = useState([]); //image 전달용 변수 (firebase -> axios server)
  /**이미지 preview */
  const saveFileImage = (e) => {
    switch (e.target.name) {
      case "home_image1":
        setImagePre({
          ...ImagePre,
          img1: URL.createObjectURL(e.target.files[0]),
        });
        return "";
      case "home_image2":
        setImagePre({
          ...ImagePre,
          img2: URL.createObjectURL(e.target.files[0]),
        });
        return "";
      default:
        setImagePre({
          ...ImagePre,
          img3: URL.createObjectURL(e.target.files[0]),
        });
        return "";
    }
  };

  const deleteFileImage = () => {
    // reset 버튼 클릭 시, 이미지 미리보기 삭제
    setImagePre("");
  };
  const ImgSubmit = async (e) => {
    // 이미지만 파이어베이스로 이동
    const files = [
      e.target.home_image1.files[0],
      e.target.home_image2.files[0],
      e.target.home_image3.files[0],
    ];

    files.forEach(async (x, i) => {
      if (x) {
        const storageRef = ref(storage, `${user.user_name}/${now}/${i}`);
        await uploadBytes(storageRef, x);
        // uploadBytes(storageRef, x).then(snapshot => { //firebase 이미지 업로드
        //     console.log('Uploaded a blob of file!')
        // })
      }
    });

    const res_list = await ImgDownload();
    return res_list;

    // setTimeout(()=>{
    //     ImgDownload();
    //     console.log("waiting for get image")
    // }, 5000)
  };

  const ImgDownload = async () => {
    let cur_list = [];
    for (let i = 0; i < 3; i++) {
      await getDownloadURL(ref(storage, `${user.user_name}/${now}/${i}`))
        .then((url) => {
          cur_list.push(url);
        })
        .catch((error) => {
          console.log(error);
        });
    }
    return cur_list;
  };

  const HandleSubmit = async (e) => {
    // Submit 이벤트
    e.preventDefault(); //페이지 이동 방지

    const list = await ImgSubmit(e); //파이어베이스 저장 후 src 가져오기

    // setList([...location.state.data.imageUrl, ...list]) //location image 가져요기

    const data = {
      home_name: e.target.home_name.value,
      lati: location.state.data.position.lat,
      longi: location.state.data.position.lng,
      location: e.target.home_spot.value,
      tag: e.target.tag.value.split("#").slice(1),
      price: e.target.price.value,
      site: e.target.home_site.value,
      reason: e.target.reason.value,
      category: e.target.category.value,
      images: [...location.state.data.imageUrl, ...list],
    };

    axios({
      method: "post",
      url: `/api/v1/community`,
      headers: {
        Authorization: `Bearer ${user.access_token}`,
      },
      data,
    })
      .then((res) => {
        console.log(res.data);
        console.log("게시글 작성에 성공");
        // alert('숙소 정보가 추가되었습니다.');
        // navigate('/');
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <AddHomeComponent
      ImagePre={ImagePre}
      saveFileImage={saveFileImage}
      deleteFileImage={deleteFileImage}
      HandleSubmit={HandleSubmit}
      loaction={location.state.data}
    />
  );
};

export default AddhomePage;
