import {IDCompleteDiv, IDCompleteName, IDCompleteSpan, IDCompleteBtn
} from './IDCompleteStyled';
import { Link, useLocation } from "react-router-dom";
import store from '../../../../Store/useStore';

function IDCompletePage() {
  const location = useLocation();
  const stores = store();

  const state = location.state;
  const user_id = state.user_id;
  const create_at = state.create_at.substr(0, 10);

  const profile_url = state.profile_url;

  return (
    <div
      style={{
        backgroundColor: '#fafafa',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%',
        padding: '5%'
      }}>


          <IDCompleteDiv>
              

              <IDCompleteName>고객님의 더맵 계정을 찾았습니다.</IDCompleteName>
              <IDCompleteSpan>아이디 확인 후 로그인해 주세요.</IDCompleteSpan>
              
              <div
              style={{
                display: 'flex',
                width: '505px',
                height: '70px',
                marginBottom: '150px'
            }}>
                {profile_url === null ? (
                                      <img style={{borderRadius: '50%'}} src={stores.profile_url} alt="profileImg" />
                                    ) : (
                                      <img style={{borderRadius: '50%'}} src={profile_url} alt="profileImg" />
                                    )}

                <div
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'space-around'
                }}>
                  <span
                  style={{
                    display: 'flex',
                      height: '36px',
                      marginLeft: '20px',
                      fontFamily: 'NotoSansKR-Bold',
                      fontSize: '25px',
                      lineHeight: '1.44',
                      letterSpacing: '-0.63px',
                      textAlign: 'left',
                      color: 'black'
                  }}
                  >{user_id}</span>
                  <span
                  style={{
                      display: 'flex',
                      height: '24px',
                      marginLeft: '20px',
                      fontFamily: 'NotoSansKR-Regular',
                      fontSize: '16px',
                      lineHeight: '1.5',
                      letterSpacing: '-0.4px',
                      textAlign: 'left',
                      color: '#767676'
                  }}
                  >가입일 {create_at}</span>

                </div>
              </div>


              


              <Link to="/password" style={{textDecorationLine: 'none'}}>
                <IDCompleteBtn>비밀번호 찾기</IDCompleteBtn>
              </Link>
              <Link to="/login" style={{textDecorationLine: 'none'}}>
                <IDCompleteBtn primary>로그인</IDCompleteBtn>
              </Link>
              
              
          </IDCompleteDiv>

    </div>
  );
}

export default IDCompletePage

