import create from 'zustand';

const useStore = create(set => ({
    inputText: '',
    searchPlace: '',
    setInputText: (text) => set({inputText: text.target.value}),
    prevent: (e) => set(e.preventDefault()),
    onSubmit: () => set(state=>({ 
        searchPlace: state.inputText, 
        inputText: ''
    })),
    searchPlaceReset: () => set({searchPlace: ''}), //검색 키워드 초기화
}));

export default useStore;