import {PWForm, SearchName, SearchBtn, FindSpan, InputBox, PWFindBtn
} from './PasswordStyled';
import { Link } from "react-router-dom";
import Modal from '../../../Modal/Modal';
import store from '../../../../Store/useStore';

function PasswordPage(
  { inputValueId,
    inputValueName,
    handleInputId,
    handleInputName,
    onSubmitHandler,
    msg,
    link
  }) {
    const state = store();
  return (
    <div
      style={{
        backgroundColor: '#fafafa',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%',
        padding: '5%'
      }}>


          <PWForm onSubmit={onSubmitHandler}>
              

              <SearchName>비밀번호 찾기</SearchName>

              <div
                style={{
                    display: 'flex',
                    width: '505px',
                    height: '43px',
                    justifyContent: 'center',
                    marginBottom: '60.5px'
                }}>

                    <Link to="/id" style={{textDecorationLine: 'none'}}>
                        <SearchBtn primary type='button'>아이디 찾기</SearchBtn>
                    </Link>
                    <Link to="/password" style={{textDecorationLine: 'none'}}>
                        <SearchBtn type='button'>비밀번호 찾기</SearchBtn>
                    </Link>
                    
                    

                </div>
              
              <FindSpan>아이디</FindSpan>
              <InputBox type='text' placeholder={'아이디를 입력해주세요'} value={inputValueId} onChange={handleInputId} ></InputBox>
              <FindSpan>이름</FindSpan>
              <InputBox type='text' placeholder={'이름을 입력해주세요'} value={inputValueName} onChange={handleInputName}></InputBox>
              

              {inputValueId === '' || inputValueName === '' ? (
                                      <PWFindBtn primary type='submit'>비밀번호 찾기</PWFindBtn>
                                    ) : (
                                      <PWFindBtn type='submit'>비밀번호 찾기</PWFindBtn>
                                    )}
              {state.open_modal ? (
                    <Modal msg={'비밀번호 찾기가 완료되었습니다.'} link={'/new-password'}></Modal>
              ) : ( null
              )}
          </PWForm>

    </div>
  );
}

export default PasswordPage

