import {
    ProfileForm, ProfileName, ProfileImg, ProfileCameraImg, ProfileSpan, InputBox, TextBtn, ProfileLabel, FollowSpan, FollowCount
  } from './ProfileChangeStyled';
import store from '../../../Store/useStore';
  


function ProfileChangePage(
{
    inputValueName,
    inputValueEmail,
    handleInputName,
    handleInputEmail,
    saveFileImage,
    fileImage,
    ImgSubmit,
    cancelProfile

}) {
    const state = store();

    return (
        <div
        style={{
          backgroundColor: '#fafafa',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          alignContent: 'spaceAround',
          height: '100%',
          padding: '5%'
        }}>
            
            <ProfileForm onSubmit={ImgSubmit}>
                <ProfileName>회원 정보 수정</ProfileName>
                

                <div
                style={{
                    display: 'flex',
                    width: '505px',
                    height: '150px',
                    justifyContent: 'center',
                    marginBottom: '31px'
                }}>
                <ProfileLabel htmlfor="img">
                    <input style={{display: 'none'}}
                                    type="file"
                                    name="img"
                                    id="img"
                                    onChange={saveFileImage}
                                    />
                                    {fileImage ? (
                                        <ProfileImg src={fileImage} alt="xx" />
                                    ) :  
                                        (state.profile_url === null ? (
                                            <ProfileImg src='./assets/icon/userImg@3x.png' alt="userImg"/>
                                        ) : (
                                            <ProfileImg src={state.profile_url} alt="userImg"/>
                                        ))
                                    }
                                    <ProfileCameraImg src="./assets/icon/cameraImgBtn.png" alt="cameraImgBtn"/>

                </ProfileLabel>

                </div>

                <div
                style={{
                    display: 'flex',
                    width: '505px',
                    height: '29px',
                    justifyContent: 'center',
                    marginBottom: '17px'
                }}>
                 
                <FollowSpan>팔로워</FollowSpan> 
                <FollowCount>5</FollowCount>
                <span
                style={{
                    display: 'flex',
                    width: '5px',
                    height: '29px',
                    marginLeft: '17px',
                    marginRight: '17px',
                    fontFamily: 'NotoSansKR-Regular',
                    fontSize: '20px',
                    color: '#767676'
                }}
                >|</span>
                <FollowSpan>팔로잉</FollowSpan>
                <FollowCount>0</FollowCount>

                </div>

                <ProfileSpan>이름</ProfileSpan>
                <InputBox type='text' placeholder={'이름을 입력해주세요'} value={inputValueName} onChange={handleInputName}></InputBox>
                <ProfileSpan>이메일</ProfileSpan>
                <InputBox type='email' placeholder={'이메일을 입력해주세요'} value={inputValueEmail} onChange={handleInputEmail}></InputBox>
                <div
                style={{
                    display: 'flex',
                    marginBottom: '20px'
                }}>
                <TextBtn primary type='button' onClick={cancelProfile}>취소</TextBtn>
                <TextBtn type='submit'>수정</TextBtn>
                </div>
                
            </ProfileForm>
        </div>
    );
  }
  
  export default ProfileChangePage;