import React, { useState } from "react";
import {Link} from 'react-router-dom'
import { Map, MapMarker } from "react-kakao-maps-sdk";
import AccInfoWindow from './accinfo/AccInfoWindow'
import {StyledAddBtn, StyledSearchMarkerInfo} from './MainStyled'

const MapContainer = ({
    state,
    markers,
    info,
    accOpen,
    markerInfo,
    setState,
    setMap,
    setInfo,
    handleMapInfo,
    handleAccOpen,
    category}) => {
    
    //숙소 카테고리에 따른 마커 이미지 설정
    const markerSrc = (category)=>{
        let src = '';
        if(category === 'hotel') src='./assets/icon/hotelMarker.png';
        else if(category === 'motel') src='./assets/icon/motelMarker.png';
        else if(category === 'pension') src='./assets/icon/pensionMarker.png';
        else if(category === 'solitary') src='./assets/icon/solitaryMarker.png';
        else if(category === 'camping') src='./assets/icon/campingMarker.png';
        else if(category === 'etc') src='./assets/icon/etcMarker.png';
        else src='./assets/icon/searchMarker.png';

        return src;
    }

    //클릭한 마커의 데이터 저장
    //즉, 숙소추가화면에 넘겨줄 데이터
    const [clickData, setClickData] = useState([]);
    
    return (
        <div>
            {(clickData.length !== 0) && (
                <Link to="/addHome" state={{ data:clickData }}>
                    <StyledAddBtn>
                            <img src="./assets/icon/addBtn.png" alt="addbtn"/>
                    </StyledAddBtn>
                </Link>
            )}
            
            <Map
                center={state.center}
                style={state.style}
                level={state.level}
                isPanto={state.isPanto}
                onCreate={setMap}
                onZoomChanged={(map)=>setState((prev)=>({...prev, level: map.getLevel()}))}
                onIdle={handleMapInfo} //중심 좌표나 확대 수준이 변경됐을 때
            >

                {markerInfo != null && (
                    markerInfo.map((data, index) => {                        
                        if(category === '' || data.category===category){
                            return(
                                <MapMarker
                                    key={index}
                                    position={{ lat: data.lati, lng: data.longi }}
                                    onClick={() =>{ handleAccOpen(index) }}
                                    image={{
                                        src: markerSrc(data.category),
                                        size: {
                                            width: "40px",
                                            height: "54px",
                                        },
                                        options: {
                                            offset: { //마커의 좌표에 일치시킬 이미지 안의 좌표
                                                x: 20,
                                                y: 60,
                                            },
                                        },
                                    }}
                                >
                                    { //accOpen 객체가 추가되고 select가 true인 마커만 숙소창 open
                                        accOpen.length !== 0 && accOpen[index] && accOpen[index].select && ( 
                                        <AccInfoWindow data={data}></AccInfoWindow>
                                    )}
                                </MapMarker>
                            )
                        }
                    })
                )}

                {markers.map((marker)=>( //검색된 장소 마커 표시
                    <MapMarker
                        key={`marker-${marker.name}-${marker.position.lat},${marker.position.lng}-${marker.address}`}
                        position={marker.position}
                        onClick={() => {
                            setInfo(marker)
                            setClickData(marker)
                            console.log(clickData)
                        }}
                        image={{
                            src: './assets/icon/searchMarker.png',
                            size: {
                                width: "40px",
                                height: "54px",
                            },
                            options: {
                                offset: { //마커의 좌표에 일치시킬 이미지 안의 좌표
                                    x: 20,
                                    y: 60,
                                },
                            },
                        }}
                        
                    > 
                        {info && info.name === marker.name && info.position === marker.position && (
                            <StyledSearchMarkerInfo>{marker.name}</StyledSearchMarkerInfo>
                        )}
                    </MapMarker>
                ))}
            </Map>
        </div>
    );
}

export default MapContainer;