import { useState } from 'react';
import SignUpComponent from './SignUp';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';


function SignUpPage(){


    const navigate = useNavigate();

    const [inputValueId, setInputValueId] = useState('');
    const [inputValuePW, setInputValuePW] = useState('');
    const [InputConfirmPW, setInputConfirmPW] = useState('');
    const [InputName, setInputName] = useState('');
    const [InputEmail, setInputEmail] = useState('');
  
    const handleInputId = (e) => {
      setInputValueId(e.target.value);
    }
  
    const handleInputPW = (e) => {
      setInputValuePW(e.target.value);
    }
    
    const handleInputConfirmPW = (e) => {
      setInputConfirmPW(e.target.value);
    }

    const handleInputName = (e) => {
      setInputName(e.target.value);
    }

    const handleInputEmail = (e) => {
      setInputEmail(e.target.value);
    }
  

    const onSubmitHandler = (e) => {
      e.preventDefault();

      axios({
        method: 'post',
        url: '/api/v1/user',
        data: {
          "user_id" : inputValueId,
          "password" : inputValuePW,
          "user_name" : InputName,
          "email" : InputEmail
        }
      })
      .then((response) => {
        console.log(response);
        if(response.data.isSuccess){
          alert('회원가입이 완료되었습니다.');
          navigate('/login')
        }
        else if(!response.data.isSuccess){
          alert(response.data.msg);
        }

        
      })
      .catch((error) => {
        console.log(error);
        console.log('실패');
      });


      if (inputValuePW !== InputConfirmPW){
        return alert('비밀번호와 비밀번호 확인이 같지 않습니다.')
      }

      console.log('Id:', inputValueId, 'PW:', inputValuePW, 'PW확인:', InputConfirmPW, '이름:', InputName, 'Email:', InputEmail)
    }
  


    const onClickSignUp = () => {

      console.log('click login');
    }





    return (
    <SignUpComponent
        inputValueId={inputValueId}
        handleInputId={handleInputId}

        inputValuePW={inputValuePW}
        handleInputPW={handleInputPW}

        InputConfirmPW={InputConfirmPW}
        handleInputConfirmPW={handleInputConfirmPW}

        InputName={InputName}
        handleInputName={handleInputName}

        InputEmail={InputEmail}
        handleInputEmail={handleInputEmail}

        onSubmitHandler={onSubmitHandler}
        onClickSignUp={onClickSignUp}

    ></SignUpComponent>

    );
}



export default SignUpPage;