import { useState } from 'react';
import PasswordComponent from './Password'
import axios from 'axios';
import store from '../../../../Store/useStore';
import { useNavigate } from 'react-router-dom';


function PasswordPage(){
    const state = store();

    const navigate = useNavigate();
    const [inputValueName, setinputValueName] = useState('');
    const [inputValueId, setInputValueId] = useState('');
    const [msg, setMsg] = useState('');
    const [link, setLink] = useState('');
    

    const handleInputId = (e) => {
        setInputValueId(e.target.value);
    }

    const handleInputName = (e) => {
        setinputValueName(e.target.value);
    }
  

    // 로그인
    const onSubmitHandler = (e) => {
      e.preventDefault();

      if (inputValueName === '' || inputValueId === ''){
        return alert('이름 또는 아이디를 입력해주세요.')
        // setMsg('이름 또는 아이디를 입력해주세요.')
        // setLink('/password')
        // state.set_open_modal(true);
        // return 
      }

      console.log('id:', inputValueId, 'name:',inputValueName)
      axios({
        method: 'get',
        url: `/api/v1/user/password/${inputValueId}/${inputValueName}`
      })
      .then((response) => {
        if (response.data.isSuccess){
            state.set_access_token(response.data.result.token);
            // setMsg(response.data.msg)
            // setLink('/new-password')
            state.set_open_modal(true);
            // alert(response.data.msg)
            // navigate('/new-password')
        }
        else if (!response.data.isSuccess){
            // setMsg(response.data.msg)
            // setLink('/password')
            // state.set_open_modal(true);
            alert(response.data.msg)
        }


      })
      .catch((error) => {
        console.log(error);
        console.log('실패');
      });


    }
  
  
    return (
     <PasswordComponent
     inputValueId={inputValueId}
     inputValueName={inputValueName}
     handleInputId={handleInputId}
     handleInputName={handleInputName}
     onSubmitHandler={onSubmitHandler}
     msg={msg}
     link={link}
    ></PasswordComponent>

    );
}



export default PasswordPage;