import create from 'zustand';
import { devtools, persist } from 'zustand/middleware';


const store = create(devtools((set, get) => ({
    access_token : null,
    user_index: '', //유저 고유 인덱스
    user_name: '', //유저 이름
    profile_url: null,
    open_modal: false,
    set_open_modal : (newOpenModal) => {
      set(() => ({open_modal : newOpenModal}))
    },
    set_profile_url : (newProfileimg) => {
      set(() => ({profile_url : newProfileimg}))
    },
    set_access_token : (newAccess_token) => set(() => ({access_token : newAccess_token})),
    setUserIndex: (inputIndex) => set({user_index: inputIndex}),
    setUserName: (inputName) => set({user_name: inputName})
  })))


export default store;