import { useEffect } from 'react';
import isLoginStore from '../../../Store/isLoginStore';
import { useNavigate } from 'react-router-dom';

export default function(SpecificComponent, option) {
    

    function AuthenticationCheck(props){
        const navigate = useNavigate();
        const state = isLoginStore();
        useEffect(() => {
            
            if(state.isLogin === null){     // 로그인 안 함
                if(option === true){        // 로그인 안 한 유저는 접근할 수 없는 페이지
                    //alert('접근할 수 없는 영역입니다.')
                    navigate('/login')
                }
            }
            else if(state.isLogin === true){    // 로그인 함
                if(option === false){       // 로그인 한 유저는 접근할 수 없는 페이지
                    //alert('접근할 수 없는 영역입니다.')
                    navigate('/')
                }
            }
            
        }, [])

        
        return (
            <SpecificComponent />
        )


    }
    
    return AuthenticationCheck
}