import './App.css';
import { ThemeProvider } from "styled-components";
import { Routes, Route } from 'react-router-dom'
import LoginPage from './components/pages/Login';
import ProfileChangePage from './components/pages/ProfileChange';
import SignUpPage from './components/pages/SignUp';
import MainPage from './components/pages/Main';
import CommunityPage from './components/pages/Community/Community';
import Auth from './components/pages/Login/auth';
import Refresh from './components/pages/Login/refresh';
import AddhomePage from './components/pages/Community/addHome';
import IDPage from './components/pages/FindIDPW/ID';
import PasswordPage from './components/pages/FindIDPW/Password';
import IDCompletePage from './components/pages/FindIDPW/IDComplete';
import NewPasswordPage from './components/pages/FindIDPW/NewPassword';
import Header from './components/base/Header';
import Footer from './components/base/Footer/Footer';
import AccModifyPage from './components/pages/Main/accinfo/AccModify';

function App() {
  const theme = {
    blueGreen: '#1a786a',
    orangeYellow: '#fcaa00',
    black: '#000',
    warmGrey: '#767676',
    pinkishGrey: '#ccc',
    whiteTwo: '#fafafa',
    white: '#fff',
  };



  // null : 아무나 사용 가능한 페이지
  // true : 로그인한 유저만 사용 가능한 페이지
  // false : 로그인한 유저는 사용 불가능한 페이지


  const AuthMainPage = Auth(MainPage, null)
  const AuthLoginPage = Auth(LoginPage, false)
  const AuthSignUpPage = Auth(SignUpPage, false)
  const AuthProfileChangePage = Auth(ProfileChangePage, true)
  const AuthCommunityPage = Auth(CommunityPage, null)
  const AuthAddhomePage = Auth(AddhomePage, null)
  const AuthIDPage = Auth(IDPage, null)
  const AuthPasswordPage = Auth(PasswordPage, null)
  const AuthIDCompletePage = Auth(IDCompletePage, null)
  const AuthNewPasswordPage = Auth(NewPasswordPage, null)

  Refresh();  // 새로고침할 때 로그인 유지가능

  return (
    <ThemeProvider theme={theme}>
      <Header />
      <Routes>
        <Route path="/" element={<AuthMainPage />} />
        <Route path="/login" element={<AuthLoginPage />} />
        <Route path="/profile-change" element={<AuthProfileChangePage />} />
        <Route path="/sign-up" element={<AuthSignUpPage />} />
        <Route path="/community" element={<AuthCommunityPage />} />
        <Route path="/addhome" element={<AuthAddhomePage />} />
        <Route path="/id" element={<AuthIDPage />} />
        <Route path="/password" element={<AuthPasswordPage />} />
        <Route path="/id-complete" element={<AuthIDCompletePage />} />
        <Route path="/new-password" element={<AuthNewPasswordPage />} />
        <Route path="/acc-modify" element={<AccModifyPage/>} />
      </Routes>
      <Footer />
    </ThemeProvider>
  );
}

export default App;
