import { useState } from 'react';
import NewPasswordComponent from './NewPassword'
import axios from 'axios';
import store from '../../../../Store/useStore';
import isLoginStore from '../../../../Store/isLoginStore';
import refreshTokenStore from '../../../../Store/refreshTokenStore';
import { useNavigate } from 'react-router-dom';

function NewPasswordPage(){
    const state = store();
    const isState = isLoginStore();
    const refreshState = refreshTokenStore();

    const navigate = useNavigate();
    const [inputValuePW, setInputValuePW] = useState('');
    const [InputConfirmPW, setInputConfirmPW] = useState('');
    const [msg, setMsg] = useState('');
    const [link, setLink] = useState('');
    

    const handleInputPW = (e) => {
        setInputValuePW(e.target.value);
    }
      
    const handleInputConfirmPW = (e) => {
        setInputConfirmPW(e.target.value);
    }
  
    

    



    // 로그인
    const onSubmitHandler = (e) => {
      e.preventDefault();

      if (inputValuePW === '' || InputConfirmPW === ''){
        return alert('비밀번호 또는 비밀번호 확인을 입력해주세요.')
      }
      if (inputValuePW !== InputConfirmPW){
        return alert('비밀번호와 비밀번호 확인이 같지 않습니다.')
      }

      axios({
        method: 'put',
        url: '/api/v1/user/password',
        headers: {
            "Authorization" : "Bearer " + state.access_token
        },
        data: {
            "password" : inputValuePW
        }
        
      })
      .then((response) => {
        if (response.data.isSuccess){
            // console.log('성공')
            // setMsg('비밀번호 변경이 완료되었습니다.')
            // setLink('/login')
            state.set_open_modal(true);
            
        }
        else if (!response.data.isSuccess){
            // setMsg(response.data.msg)
            // setLink('/new-password')
            alert(response.data.msg)
            state.set_open_modal(true);
        }

        
        

      })
      .catch((error) => {
        console.log(error);
        console.log('실패');
      });

    }
  
  
    return (
     <NewPasswordComponent
    inputValuePW={inputValuePW}
    setInputValuePW={setInputValuePW}
    handleInputPW={handleInputPW}

    InputConfirmPW={InputConfirmPW}
    setInputConfirmPW={setInputConfirmPW}
    handleInputConfirmPW={handleInputConfirmPW}
    onSubmitHandler={onSubmitHandler}
    msg={msg}
    link={link}
    ></NewPasswordComponent>

    );
}



export default NewPasswordPage;