import create from 'zustand';
import { devtools, persist } from 'zustand/middleware';


const isLoginStore = create(devtools(
    persist(
      (set) => ({
        isLogin : null,
        trueIsLogin : () => {
          set(() => ({isLogin : true}))
        },
        nullIsLogin : () => {   
          set(() => ({isLogin : null}))
        }
  
      }),
      {
        name : 'isLogin',
        getStorage: () => sessionStorage
      }
  )
  ))

  export default isLoginStore;
  