import React, { useState } from "react";
import {
  StyledBackground,
  StyledTitle,
  StlyedUnderline,
  StyledContaniner,
  StyledHomeImg,
  StyledFlex,
  StyledProfileBox,
  StyledProfile,
  StyledImgBox,
  StyledImg,
  StyledSlide,
  StyledHomeTitle,
  StyledHomeAddress,
  StyledSiteReason,
  StyledInnervalue,
  StyledTag,
  StyledInnerTag,
  StyledPrice,
} from "../StyledBoard";

const GridItem = ({ x }) => {
  const [img_num, set_img_num] = useState(1);
  const prevSlide = () => {
    if (img_num === 0) {
      set_img_num(x.imgs.length); // 마지막 사진으로 넘어갑니다.
    } else {
      set_img_num(img_num - 1);
    }
  };

  const nextSlide = () => {
    if (x.imgs.length - 1 <= img_num) {
      set_img_num(0); // 1번째 사진으로 넘어갑니다.
    } else {
      set_img_num(img_num + 1);
    }
  };

  const PriceComa = (price) => {
    return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <>
      <StyledContaniner className="communityBoard korRegular" key={x.id}>
        <StyledHomeImg>
          <StyledImgBox style={{ transform: `translateX(-${img_num}00%)` }}>
            {!x.imgs[0] ? (
              <StyledImg
                src="./assets/icon/addimageBtn.png"
                alt="숙소 이미지"
              />
            ) : (
              x.imgs.map((img) => {
                return (
                  <StyledImg key={img.image_id} src={img.address} alt="xx" />
                );
              })
            )}
          </StyledImgBox>
          <StyledProfileBox>
            <StyledProfile
              src={x.profile_url ? x.profile_url : "./assets/icon/userImg.png"}
              alt="xx"
            />
            {x.user_name}
            {!(x.follow === "UNFOLLOW") ? (
              ""
            ) : (
              <img
                src="./assets/icon/communityFollow.png"
                style={{ marginLeft: "15px" }}
              />
            )}
          </StyledProfileBox>
          <StyledSlide
            src="./assets/icon/communityImgLeft.png"
            id="left"
            onClick={prevSlide}
          />
          <StyledSlide
            src="./assets/icon/communityImgRight.png"
            id="right"
            onClick={nextSlide}
          />
        </StyledHomeImg>
        <StyledHomeTitle>{x.home_name}</StyledHomeTitle>
        <StyledHomeAddress>
          <img src="./assets/icon/communityAddr.png" alt="xx" />
          {x.location}
        </StyledHomeAddress>
        <StyledSiteReason>
          예약사이트
          <StyledInnervalue id="site">{x.site}</StyledInnervalue>
        </StyledSiteReason>
        <StyledSiteReason>
          찜한 이유
          <StyledInnervalue>{x.reason}</StyledInnervalue>
        </StyledSiteReason>
        <StyledTag>
          {!x.tags
            ? ""
            : x.tags.map((tag) => {
                return (
                  <StyledInnerTag key={tag.tag_id}>
                    #{tag.tag_name}
                  </StyledInnerTag>
                );
              })}
        </StyledTag>
        <StyledPrice>{PriceComa(x.price)}원</StyledPrice>
      </StyledContaniner>
    </>
  );
};

export default GridItem;
