import create from 'zustand';
import { devtools, persist } from 'zustand/middleware';

const refreshTokenStore = create(devtools(
    persist(
      (set) => ({
        refresh_token : null,
        set_refresh_token : (newRefresh_token) => {
            set(() => ({refresh_token : newRefresh_token}))
        },
  
      }),
      {
        name : 'refresh_token',
        getStorage: () => localStorage
      }
  )
  ))

  export default refreshTokenStore;
  
