import React from "react";
import SearchStyle from "./SearchStyle";

const SearchBox = ({ inputText, setInputText, prevent, onSubmit}) => {
  return (
    <SearchStyle onSubmit={(e)=>{prevent(e); onSubmit();}}>
      <input placeholder="숙소, 태그 검색" 
        onChange={setInputText}
        value={inputText}
      ></input>
      <button type="submit"></button>
    </SearchStyle>
  );
};

export default SearchBox;
