import React, { useState, useEffect } from "react";
import BoardComponent from "./board";
import axios from "axios";

const BoadrAtom = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [data, setData] = useState();
  const [last_board_id, set_last_board_id] = useState(30000);

  useEffect(() => {
    axios
      .get(`/api/v1/community/${last_board_id}`)
      .then((res) => {
        setData(res.data.result.community);
      })
      .catch((Err) => console.log(Err));
  }, []);

  const lastSlide = 5;

  useEffect(() => {
    if (data !== undefined) {
      set_last_board_id(data[data.length - 1].id);
    }
  }, [data]);

  const PriceComa = (price) => {
    return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  const prevSlide = () => {
    if (currentSlide === 0) {
      setCurrentSlide(lastSlide); // 마지막 사진으로 넘어갑니다.
    } else {
      setCurrentSlide(currentSlide - 1);
    }
  };

  const nextSlide = () => {
    if (currentSlide >= lastSlide) {
      setCurrentSlide(0); // 1번째 사진으로 넘어갑니다.
    } else {
      setCurrentSlide(currentSlide + 1);
    }
  };

  const Follow = () => {
    console.log("follow");
  };
  return (
    <BoardComponent
      data={data}
      PriceComa={PriceComa}
      currentSlide={currentSlide}
      prevSlide={prevSlide}
      nextSlide={nextSlide}
      Follow={Follow}
    />
  );
};

export default BoadrAtom;
