import { useState } from 'react';
import IDComponent from './ID'
import axios from 'axios';
import store from '../../../../Store/useStore';
import isLoginStore from '../../../../Store/isLoginStore';
import refreshTokenStore from '../../../../Store/refreshTokenStore';
import { useNavigate } from 'react-router-dom';


function IDPage(){
    const state = store();
    const isState = isLoginStore();
    const refreshState = refreshTokenStore();

    const navigate = useNavigate();
    const [inputValueName, setinputValueName] = useState('');
    const [inputValueEmail, setinputValueEmail] = useState('');
    

    const handleInputName = (e) => {
        setinputValueName(e.target.value);
    }
  
    const handleInputEmail = (e) => {
        setinputValueEmail(e.target.value);
    }


    



    // 로그인
    const onSubmitHandler = (e) => {
      e.preventDefault();

      if (inputValueName === '' || inputValueEmail === ''){
        return alert('아이디 또는 이메일 주소를 입력해주세요.')
      }

      axios({
        method: 'get',
        url: `/api/v1/user/user-id/${inputValueName}/${inputValueEmail}`
      })
      .then((response) => {
        
        if (response.data.isSuccess){
          navigate('/id-complete', {
            state: {
              user_id: response.data.result.user_id,
              create_at: response.data.result.create_at,
              profile_url: response.data.result.profile_url
            }
          })
        }
        else if (!response.data.isSuccess){
          alert(response.data.msg);
        }
        
      })
      .catch((error) => {
        console.log(error);
        console.log('실패');
        
      });


    }
  
  
    return (
     <IDComponent
     inputValueName={inputValueName}
     setinputValueName={setinputValueName}
     inputValueEmail={inputValueEmail}
     setinputValueEmail={setinputValueEmail}
     handleInputName={handleInputName}
     handleInputEmail={handleInputEmail}
     onSubmitHandler={onSubmitHandler}
    ></IDComponent>

    );
}



export default IDPage;