import { useState } from 'react';
import ProfileComponent from './ProfileChange'
import axios from 'axios';
import store from '../../../Store/useStore';
import isLoginStore from '../../../Store/isLoginStore';
import refreshTokenStore from '../../../Store/refreshTokenStore';
import { useNavigate } from 'react-router-dom';
import { imgStorage } from './addProfileFB';

import { uploadBytes, ref, getDownloadURL} from "firebase/storage";
import { async } from '@firebase/util';


function ProfileChangePage(){
    const state = store();
    const isState = isLoginStore();
    const refreshState = refreshTokenStore();

    const navigate = useNavigate();
    const [inputValueName, setinputValueName] = useState(state.user_name);
    const [inputValueEmail, setinputValueEmail] = useState('');


    //파일 미리볼 url을 저장해줄 state
    const [fileImage, setFileImage] = useState("");
    const [fileImg, setFileImg] = useState("");
    // const [url, setUrl] = useState(state.profile_url);

    // 파일 저장
    const saveFileImage = (e) => {
      setFileImage(URL.createObjectURL(e.target.files[0]));
      setFileImg(e.target.files[0])

      
    };
    
    // 프로필 페이지 나가기
    const cancelProfile = () => {
      navigate('/')
    };


    const ImgSubmit = async(e) => { // 이미지만 파이어베이스로 이동
      e.preventDefault() // 페이지 이동 방지
      
      if (!inputValueName){
        return alert('이름이 입력되어있지 않습니다.')
      }
      else if (!inputValueEmail){
        return alert('이메일이 입력되어있지 않습니다.')
      }
      
      const file = fileImg
      const storageRef = ref(imgStorage, `profileimg/${state.user_name}`)
      console.log(file)
      if (file) {
          await uploadBytes(storageRef, file).then(snapshot => { //firebase 이미지 업로드
              // console.log('Uploaded a blob of file!')
          })
          
      }

      const gsReference = ref(imgStorage, `gs://${process.env.REACT_APP_FB_STORAGE_BUCKET}/profileimg/${state.user_name}`);
      await getDownloadURL(gsReference) //firebase 이미지 url 받기
          .then(url => {
              state.set_profile_url(url)
              axios({
                method: 'put',
                url: '/api/v1/user/profile',
                headers: {
                  "Authorization" : "Bearer " + state.access_token
                },
                data: {
                  "user_name" : inputValueName,
                  "email" : inputValueEmail,
                  "profile_url" : url
                }
              })
              .then((response) => {
                console.log(response);
                if(response.data.isSuccess){
                  alert('수정 완료되었습니다.')
                }
                else if(!response.data.isSuccess){
                  alert(response.data.msg);
                }
                
                // navigate('/')
        
              })
              .catch((error) => {
                console.log(error);
                console.log('실패');
              });
          })
          .catch(err => {
              console.log(err);
          })

  }

  
    const handleInputName = (e) => {
        setinputValueName(e.target.value);
    }
  
    const handleInputEmail = (e) => {
        setinputValueEmail(e.target.value);
    }
  


  
    return (
     <ProfileComponent
        inputValueName={inputValueName}
        inputValueEmail={inputValueEmail}
        handleInputName={handleInputName}
        handleInputEmail={handleInputEmail}
        saveFileImage={saveFileImage}
        fileImage={fileImage}
        ImgSubmit={ImgSubmit}
        cancelProfile={cancelProfile}
    ></ProfileComponent>

    );
}



export default ProfileChangePage;