import create from 'zustand';
import { devtools, persist } from 'zustand/middleware';

const autoLoginStore = create(devtools(
    persist(
      (set) => ({
        autoIsLogin : null,
        trueAutoIsLogin : () => {
          set(() => ({autoIsLogin : true}))
        },
        nullAutoIsLogin : () => {   
          set(() => ({autoIsLogin : null}))
        }
      }),
      {
        name : 'autoIsLogin',
        getStorage: () => localStorage
      }
  )
  ))

  export default autoLoginStore;
  