import SearchBox from "./SearchBox";
import useStore from './placeStore'

const SearchIndex = ({}) => {
  const { inputText, setInputText, prevent, onSubmit } = useStore(state=>state)
  
  return (
    <>
      <SearchBox
        inputText={inputText}
        setInputText={setInputText}
        prevent={prevent}
        onSubmit={onSubmit}
        ></SearchBox>
    </>
  );
};

export default SearchIndex;
