import { ModalAll, ModalBody, SplitLine, ModalBtn
} from './ModalStyled';
import { Link } from 'react-router-dom';
import store from '../../Store/useStore';

function Modal(props) {

    const {msg, link} = props;
    const state = store();
    const onCloseHandler = () => {
      state.set_open_modal(false);
    }

    return (
        
      <div style={{
        display: 'flex',
        backgroundColor: 'rgb(0, 0, 0, 0.25)',
        justifyContent: 'center',
        alignItems: 'center',
        top: '0',
        right: '0',
        bottom: '0',
        left: '0',
        zIndex: '99',
        position: 'fixed'
        
      }}>
        <ModalAll>
            
          <ModalBody>{msg}</ModalBody>
          <SplitLine />
          
          <Link to={link} style={{textDecorationLine: 'none'}}>
            <ModalBtn onClick={onCloseHandler}>확인</ModalBtn>
          </Link>
        </ModalAll>
      </div>
    );
}

export default Modal;