import React, { useState } from "react";
import { Link } from "react-router-dom";
import { UserInfoStyle, FollowWrapper } from "./UserInfoStyle";
import Follow from "./Follow";

function UserInfoComponent({ 
  state, 
  follow_count, 
  following_count, 
  followInfo, 
  followerSelect,
  followingSelect,
  onClickLogout, 
  handlesetFollowInfo,
  clickFollower,
  clickFollowing, }) {
  return (
    <>
      <UserInfoStyle>
        <div className="userinfo_wrapper">
          <div className="userinfo_left">
            {state.profile_url === null ? (
                  <img style={{borderRadius: '50%'}}
                  src="./assets/icon/userImg@2x.png"
                  alt="userimg2x" />
                ) : (
                  <img style={{borderRadius: '50%'}}
                  src={state.profile_url}
                  alt="icon" />
                )}
          </div>
          <div className="userinfo_right">
            <div>
              {state.user_name}
              <Link to="./profile-change">
                <img
                  src="./assets/icon/myPageModiBtn.png"
                  alt="mypagemodibtn"
                ></img>
              </Link>
              <img
                className="userinfo_right_logout"
                src="./assets/icon/logOutBtn.png"
                alt="logoutbtn" onClick={onClickLogout}
              ></img>
            </div>
            <FollowWrapper onClick={(e) => handlesetFollowInfo(e)}>
              팔로워 {follow_count} | 팔로잉 {following_count}
            </FollowWrapper>
            {followInfo ? 
              <Follow 
                followerSelect={followerSelect}
                followingSelect={followingSelect}
                clickFollower={clickFollower}
                clickFollowing={clickFollowing}/> : 
              <></>}
          </div>
        </div>
      </UserInfoStyle>
    </>
  );
}

export default UserInfoComponent;
