import { useState } from 'react';
import LoginComponent from './Login'
import axios from 'axios';
import store from '../../../Store/useStore';
import isLoginStore from '../../../Store/isLoginStore';
import refreshTokenStore from '../../../Store/refreshTokenStore';
import autoLoginStore from '../../../Store/autoLoginStore';
import { useNavigate } from 'react-router-dom';


function LoginPage(){
    const state = store();
    const isState = isLoginStore();
    const refreshState = refreshTokenStore();
    const autoState = autoLoginStore();

    const navigate = useNavigate();
    const [inputValueId, setInputValueId] = useState('');
    const [inputValuePW, setInputValuePW] = useState('');
    const [checkedItem, setCheckedItem] = useState(false);

    const handleInputId = (e) => {
      setInputValueId(e.target.value);
    }
  
    const handleInputPW = (e) => {
      setInputValuePW(e.target.value);
    }
  

    const checkedItemHandler = (e) => {
      if (e.target.checked) {
        setCheckedItem(true);
      } 
      else {
        setCheckedItem(false);
      }
    };


    

    // 로그인
    const onSubmitHandler = (e) => {
      e.preventDefault();

      axios({
        method: 'post',
        url: '/api/v1/user/login',
        data: {
          "user_id" : inputValueId,
          "password" : inputValuePW,
        }
      })
      .then((response) => {
        console.log(response)
        if (response.data.isSuccess){
          state.set_access_token(response.data.result.access_token);  // access 토큰 저장
          refreshState.set_refresh_token(response.data.result.refresh_token);   // refresh 토큰 저장
          state.setUserIndex(response.data.result.user_idx);
          state.setUserName(response.data.result.user_name);
          state.set_profile_url(response.data.result.profile_img);
          //console.log(response.data.result.access_token)

          isState.trueIsLogin();  // 로그인 상태 store에 저장

          if(checkedItem){
            autoState.trueAutoIsLogin();
          }
          navigate('/')

        }
        else {
          alert(response.data.msg);
        }

        
      })
      .catch((error) => {
        console.log(error);
        console.log('실패');
      });
    }
  


    
  
    return (
     <LoginComponent
        handleInputId={handleInputId}
        setInputValueId={setInputValueId}
        handleInputPW={handleInputPW}
        setInputValuePW={setInputValuePW}
        onSubmitHandler={onSubmitHandler}
        checkedItemHandler={checkedItemHandler}
    ></LoginComponent>

    );
}



export default LoginPage;