import { useEffect } from 'react';
import store from '../../../Store/useStore';
import isLoginStore from '../../../Store/isLoginStore';
import refreshTokenStore from '../../../Store/refreshTokenStore';
import autoLoginStore from '../../../Store/autoLoginStore';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';



export default function Refresh() {
  const state = store();
  const isState = isLoginStore();
  const refreshState = refreshTokenStore();
  const autoState = autoLoginStore();
  const navigate = useNavigate();
  /*
      const customHttp = axios.create({
        baseURL: 'http://qkrtmfqls.gabia.io/',
        timeout: 8000,
        headers: {
          "Authorization" : "Bearer " + refreshState.refresh_token
        },
      });
  
        //   const res = customHttp.get('api/v1/user/auto-login')
  */

  useEffect(() => {
    if (!isState.isLogin && autoState.autoIsLogin) {
      isState.trueIsLogin();
      window.location.reload();
    }
    if (refreshState.refresh_token && isState.isLogin && !state.access_token) {
      axios({
        method: 'get',
        url: '/api/v1/user/auto-login',
        headers: {
          "Authorization": "Bearer " + refreshState.refresh_token
        }
      })
        .then(function (response) {
          if (response.data.isSuccess) {  // 성공하면 access token 재발급
            state.set_access_token(response.data.result.access_token);

            isState.trueIsLogin();
            state.setUserIndex(response.data.result.user_idx); //토큰 다시 받아올때마다 유저 정보 저장
            state.setUserName(response.data.result.user_name);
            state.set_profile_url(response.data.result.profile_img);

          }
          else if (!response.data.isSuccess) {
            alert(response.data.msg)
            navigate('/login')
          }

        })
        .catch(function (error) {
          console.log('실패');
        });
    }


  }, [])



}

