import React from "react";
import {Wrap} from './AccInfoStyled'
import Slider from "react-slick";

const Slide = ({image}) => {
    //슬라이더 기능 조정 setting
    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };

    let get = [];
    for(let i=0; i<image.length; i++) {
        get[i] = `${image[i].address}`;
    }

    return (
        <Wrap>
            <Slider {...settings}>
                {get.map(data => <div key={data}><img className='accImg' src={data} alt="loading"></img></div>)}
            </Slider>
        </Wrap>
    );
}

export default Slide;