import React from 'react';
import { Link } from 'react-router-dom';
import StyledAccInfoWrap, { StyledDesc } from './AccInfoStyled'
import Slider from './Slick';

const AccInfoWindow = ({data}) => {
    console.log(data);

    const tagPut = () => {
        //받아온 태그 이름 #붙여서 넣기
        let input = []; 
        for(let i=0; i<data.tags.length; i++) {
            input[i] = '#' + data.tags[i].tag_name;
        }

        //저장된 태그 이름들에 span 태그 붙이기
        const tmp = input.map(t => <span key={t}>{t}</span>);
        return (
            <>
                {tmp}
            </>
        );
    }

     //가격에 콤마 붙이기
     const price = data.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    
    return (
        <StyledAccInfoWrap>
            <div className='name'>{data.home_name}</div>
            <Slider image={data.imgs}></Slider>

            <div className='location'>
                <img src='./assets/icon/infoWindowPosIcon.png' alt='positioncon'/>
                <span>{data.location}</span>
            </div>
            <div className='tags'>
                {tagPut()}
            </div>
            <div className='desc'>
                <StyledDesc leftpadding={"19px"}>
                    가격
                    <span className="coast">{price}</span>
                    원
                </StyledDesc>
                <StyledDesc>
                    예약사이트
                    <span className="reservation">{data.site}</span>
                </StyledDesc>
                <StyledDesc leftpadding={"5px"} bottompadding={"10px"}>
                    찜한 이유
                    <span className="reason">{data.reason}</span>
                </StyledDesc>
            </div>
        
            <div className='controlBtn'>
                <Link to="/acc-modify" state={{ accData: data }}><img id='modify' src='./assets/icon/modiBtn.png' alt='modify'/></Link>
                <img id='delete' src='./assets/icon/deleteBtn.png' alt='delete'/>
            </div>
        </StyledAccInfoWrap>
    );
}

export default AccInfoWindow;